import * as THREE from "three";
import React, { useRef, useState, useEffect } from "react";
import {
  Canvas,
  useThree,
  useRender,
  useLoader,
  extend,
} from "react-three-fiber";
import { useTransition, a } from "react-spring";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader";
import { Link } from "react-router-dom";

const home = () => {
  extend({ OrbitControls });
  const Controls = (props) => {
    const { gl, camera } = useThree();
    const ref = useRef();
    useRender(() => ref.current.update());
    return (
      <orbitControls ref={ref} args={[camera, gl.domElement]} {...props} />
    );
  };

  function Model({ url }) {
    const model = useLoader(GLTFLoader, url, (loader) => {
      const dracoLoader = new DRACOLoader();
      dracoLoader.setDecoderPath("/draco-gltf/");
      loader.setDRACOLoader(dracoLoader);
    });
    return (
      <group
        rotation={[-Math.PI / 2, 0, 0]}
        position={[0, -7, 0]}
        scale={[7, 7, 7]}
      >
        {model.map(({ geometry, material }) => {
          // There are two buffergeometries in this gltf
          // Save some GPU by rendering the rocks a little less vivd than the rocket
          const rocks = geometry.index.count < 80000;
          const Material = rocks
            ? "meshLambertMaterial"
            : "meshStandardMaterial";
          return (
            <mesh
              key={geometry.uuid}
              rotation={[Math.PI / 13.5, -Math.PI / 5.8, Math.PI / 5.6]}
              geometry={geometry}
              castShadow={!rocks}
              receiveShadow={!rocks}
            >
              <Material attach="material" map={material.map} roughness={1} />
            </mesh>
          );
        })}
      </group>
    );
  }

  function Loading() {
    const [finished, set] = useState(false);
    const [width, setWidth] = useState(0);

    useEffect(() => {
      THREE.DefaultLoadingManager.onLoad = () => set(true);
      THREE.DefaultLoadingManager.onProgress = (url, itemsLoaded, itemsTotal) =>
        setWidth((itemsLoaded / itemsTotal) * 200);
    }, []);

    const props = useTransition(finished, null, {
      from: { opacity: 1, width: 0 },
      leave: { opacity: 0 },
      update: { width },
    });

    return props.map(
      ({ item: finished, key, props: { opacity, width } }) =>
        !finished && (
          <a.div className="loading" key={key} style={{ opacity }}>
            <div className="loading-bar-container">
              <a.div className="loading-bar" style={{ width }} />
            </div>
          </a.div>
        )
    );
  }

  return (
    <>
      <div className="bg" />
      <Canvas camera={{ position: [0, 0, 15] }} shadowMap>
        <ambientLight intensity={1.5} />
        <pointLight intensity={2} position={[-10, -25, -10]} />
        <spotLight
          castShadow
          intensity={1.25}
          angle={Math.PI / 8}
          position={[25, 25, 15]}
          shadow-mapSize-width={2048}
          shadow-mapSize-height={2048}
        />
        <fog attach="fog" args={["#cc7b32", 16, 20]} />
        <Model url="/scene-draco.gltf" />
        <Controls
          autoRotate
          enablePan={false}
          enableZoom={false}
          enableDamping
          dampingFactor={0.5}
          rotateSpeed={1}
          maxPolarAngle={Math.PI / 2}
          minPolarAngle={Math.PI / 2}
        />
      </Canvas>
      <div className="layer">
        <h1>Aarsh Patel</h1>
      </div>
      <Loading />
      <a
        target="blank"
        href="https://drive.google.com/file/d/16JZzwJ37GjS1XlL0aZ4o25UIcTKexLki/view"
        className="top-left"
        children="Résumé"
      />
      <Link push to="/projects" className="top-right" children="Projects" />
      <a
        target="blank"
        style={{ color: "rgba(177, 164, 164, 0.56)", fontSize: "150%" }}
        href="https://github.com/pmndrs/react-three-fiber"
        className="bottom-right"
        children="Source"
      />
    </>
  );
};
export default home;
